* {
  box-sizing: border-box;
}

body {
  font-family: 'Source Sans Pro', 'Trebuchet MS', 'Lucida Grande', 'Bitstream Vera Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  text-align: center;
  color: #293c4b;
}

.wrapper {
  display: flex;
  justify-content: center;
  padding: 0 2em;
}

h2 { margin: 0; }

.drop-area {
  width: 15rem;
  height: 15rem;
  max-width: 30vw;
  max-height: 30vh;
  border: 1px solid #333;
  background-color: #ddd;
}

.drop-area.drag-over {
  background-color: #aaa;
}

.drop-area input[type="file"] {
  display: none;
}

.drop-area > label {
  cursor: pointer;
}

.files {
  margin-left: 2em;
}

.header {
  display: flex;
  justify-content: space-between;
}

.files > ul {
  padding: 0;
}

.files > ul > li:nth-child(2n) {
  background-color: rgba(0,0,0,0.05);
}

.files > ul > li {
  display: flex;
  padding: 1em;
}

.files > ul > li p {
  margin-top: 0;
}

.preview > img {
  max-width: 20vw;
  max-height: 20vh;
}

.info {
  display: flex;
  flex-direction: column;
  margin: 0 1em;
  flex: 1 0 4em;
}

.files > ul > li textarea {
  height: 5em;
}

.unknown {
  flex-direction: column;
}

.unknown > p {
  margin-bottom: 0;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  padding: 2em;

  display: flex;
  flex-direction: column;
  align-items: flex-end;

  background-color: rgba(0,0,0,0.5);
}

.modal > .content {
  flex: 1 0 4em;
  width: 100%;
}

.modal > button {
  margin-bottom: 1em;
  flex: 0 0 3em;
  width: 5em;
}
